.home-page {
  height: 100%;
  width: 100%;
  padding: 10% 4vw;
}

.ant-tag {
  margin-bottom: 4px;
}

.profile-title {
  margin-bottom: 0em;
  font-weight: 200;
  font-size: 38px;
}

.section-title {
  margin-bottom: 0em;
  font-weight: 300;
  font-size: 24px;
  text-transform: lowercase;
}

.section-div {
  margin-bottom: 0.5em;
}

.project-title {
  margin-bottom: 0em;
  font-weight: 600;
  font-size: 18px;
}

.card {
  margin-right: 16px;
  margin-top: 16px;
  background: #fafafa;
  border-radius: 4px;
  padding: 8px;
  box-shadow: 0px 0px 5px 0px #aaa;
}
.sub-accent {
  color: #4e4e4e
}

.accent {
  color: #096dd9;
}

.profile-employer {
  text-transform: uppercase;
}

.profile-subtitle {
  font-weight: 300;
}

.profile-picture {
  border-radius: 50%;
  margin-bottom: 16px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
}

body {
  background: #f2f2f2;
}

.profile-button {
  font-size: 24px;
  margin-top: 16px;
  margin-right: 16px;
  color: rgba(0, 0, 0, 0.5);
 }
 .ant-btn {
   padding: 0px;
 }

 .ant-list-header {
   font-weight: bold;
   color: #4e4e4e;
 }